@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #40A240;
}

.App {
  text-align: center;
}

header {
  padding: 10px 30px;
  display: flex;
  justify-content: space-between;
  border-bottom: 5px double #232323;
  background-color: #e1e1e1;
}

#logo {
  display: flex;
  align-items: center;
  font-family: 'Dancing Script', cursive;
  font-weight: 700;
}

header img {
  width: 60px;
  border-radius: 100%;
  margin-right: 5px;
}

header a {
  text-decoration: none;
  color: #232323;
}

#home h2 {
  color: #f4f4f4;
  background-color: #232323;
  padding: 10px;
  margin: 10px 10px 0 10px;
  border-radius: 20px 20px 0 0;
  color: #6A8A49;
}

#logoutBtn {
  position: absolute;
  right: 15px;
  top: 15px;
}

.clearfix::after {
  content: "";
  display: block;
  clear: both;
}

#search-component {
  display: flex;
  align-items: center;
}

#sign-in {
  display: flex;
  align-items: center;
}

#sign-in a {
  text-decoration: none;
  color: #232323;
  font-size: 18px;
  font-weight: 600;
}

#home-page {
  margin: 0 10px 10px 10px;
  display: flex;
  flex-flow: row wrap;
}

.movies-array {
  border: 5px double #232323;
  width: 25%;
}

.movies-array img {
  width: 100%;
}

.movies-array div {
  padding: 5px;
  font-weight: 600;
  color: #232323;
  background-color: #f4f4f4;
}

/* registration & login */
#registration-form button, 
#login-form button, 
#logoutBtn {
  padding: 5px;
  margin: 10px;
  background-color: #232323;
  color: #6A8A49;
  border-radius: 20px;
  outline: none;
}

#registration-form button:hover, 
#login-form button:hover, 
#logoutBtn:hover {
  color: #FCD650;
}

.form-input {
  padding: 2px;
  display: flex;
  text-align: left;
  width: 325px;
  margin: 2px auto;
}

.form-input input {
  margin-left: auto;
}

.error-msg {
  color: red;
  margin: 2px;
}

#registration-form h4,
#login-form h4 {
  background-color: #232323;
  color: #6A8A49;
  padding: 10px;
  margin: 40px 0;
  border-radius: 20px 20px 0 0;
}

#registration-form,
#login-form {
  width: 350px;
  border-bottom: 5px double #232323;
  border-radius: 20px 20px 0 0;
  margin: 0 auto;
  background-color: #e1e1e1;
}

/* movie list */
#mov-content {
  width: 80%;
  margin: 0 auto;
  background-color: #e1e1e1;
  border-radius: 20px 20px 0 0;
}

#mov-content a {
  text-decoration: none;
  color: #232323;
}

#mov-content a:hover {
  color: #FCD650;
}

#nav-mov {
  background-color: #232323;
  display: flex;
  justify-content: space-evenly;
  padding: 15px;
  border-radius: 20px 20px 0 0;
  margin: 2px 0;
}

#nav-mov a {
  text-decoration: none;
  color: #6A8A49;
}

#nav-mov a:hover {
  color: #FCD650;
}

#cinemas {
  margin-top: 10px;
  margin-bottom: 10px;
}

#now-showing,
#coming-soon {
  padding: 15px;
}

#now-showing h4,
#coming-soon h4 {
  margin: 5px;
}

#now-showing p, 
#coming-soon p {
  text-align: left;
  margin: 5px;
}

.buyoutBtn {
  padding: 5px;
  margin: 5px;
  float: right;
  background-color: #232323;
  color: #6A8A49;
  border-radius: 20px;
  outline: none;
}

.buyoutBtn:hover {
  color: #FCD650;
}

#purchase-box {
  position: fixed;
  left: 10%;
  top: 20vh;
  background-color: #223322;
  border-radius: 8px;
  border: 4px double #f4f4f4;
  color: #f4f4f4;
  padding: 15px;
  opacity: 0.9;
}

#purchase-box button {
  padding: 5px;
  margin: 5px;
}

#close-cart {
  border-radius: 100%;
  font-weight: 900;
  position: relative;
  left: 39px;
}

#purchase-box h3 {
  border-bottom: 4px double #232323;
  padding-left: 27px;
}

.label {
  font-weight: 700;
}

#seats {
  width: 780px;
  margin: 0 auto;
}

.seat-map {
  width: 66px;
  margin: 5px;
  padding: 5px;
  background-color: cornflowerblue;
  border-radius: 5px;
  display: inline-block;
  color: #f4f4f4;
}

/* product info */
#product-info {
  display: flex;
  justify-content: center;
}

#add-movie {
  width: 300px;
  margin: 30px;
}

.movie-info {
  padding: 2px;
  text-align: left;
  width: 100%;
  display: flex;
}

.movie-status {
  margin-left: auto;
}

.movie-info input, 
.movie-info select, 
.movie-info textarea {
  margin-left: auto;
}

.movie-info span {
  margin-left: 4px;
}

#add-movie h3, 
#edit-movie h3 {
  color: #f4f4f4;
  background-color: #232323;
  padding: 10px;
  border-radius: 20px 20px 0 0;
}

#add-movie button {
  float: right;
  margin-right: 2px;
  padding: 5px;
  background-color: #232323;
  color: #6A8A49;
  border-radius: 20px;
  outline: none;
}

#edit-movie {
  margin: 30px;
  width: 600px;
}

#edit-movie a {
  text-decoration: none;
  color: #232323;
}

#edit-movie a:hover {
  color: #FCD650;
}

#edit-form {
  display: flex;
  flex-wrap: wrap;
}

.edit-btn {
  margin-left: auto;
}

.edit-btn, 
.delete-btn {
  margin-right: 2px;
  padding: 5px;
  background-color: #232323;
  color: #6A8A49;
  border-radius: 20px;
  outline: none;
}

/* search box */
.searchbox {
  padding: 10px;
  border: none;
  outline: none;
  background-color: #232323;
  color: #f4f4f4;
  border-radius: 8px 0 0 8px;
  font-size: 20px;
  font-weight: 300;
}

#search-component input {
  width: 40vw;
}

#search-component button {
  padding: 10px;
  border: none;
  border-radius: 0 8px 8px 0;
  background-color: #C4C5C7;
  outline: none;
  font-size: 20px;
  font-weight: 300;
}